<template>
    <div>
      <h1 class="text-center">Registrar Libro</h1>
      <v-form ref="form" v-model="valid">
        <v-container>
          <v-row justify="center">
            <v-col cols="12" md="9">
              <v-text-field
                v-model="form.title"
                :rules="requireRules"
                label="Nombre libro"
                required
              ></v-text-field>
              <v-text-field
                v-model="form.author"
                :rules="requireRules"
                label="Autor"
                required
              ></v-text-field>
              <v-text-field
                v-model="form.image"
                :rules="requireRules"
                label="Src imagen"
                required
              ></v-text-field>
              <v-select
              v-model="form.type"
              :items="types"
              label="Tipo"
              :rules="requireRules"
              required
          ></v-select>
  
  
              <v-btn
                :disabled="!valid"
                color="success"
                class="mr-4"
                @click="createBook"
              >
                Registrar
              </v-btn>
  
              <v-btn color="error" class="mr-4" @click="reset">
                Limpiar formulario
              </v-btn>
  
              
            </v-col>
          </v-row>
        </v-container>
      </v-form>
    </div>
  </template>
  
  <script>
  import {db} from '@/firebase/firebase.js'
  import {collection, addDoc} from 'firebase/firestore'
  export default {
    name: "register-book",
    // props: {},
    data: function () {
      return {
        valid: true,
        form:{
          title:'',
          author:'',
          type:'',
          image:''
        },
        requireRules: [
          (v) => !!v || "Campo es requerido",
        ],
        types:['Ciencia ficción', 'novela']
        
       
      };
    },
    // computed: {},
    methods: {
      validate() {
        this.$refs.form.validate();
      },
      reset() {
        this.$refs.form.reset();
      },
      async createBook(){
          //this.validate()
          this.$refs.form.validate();
          if(!this.valid) return //si no esta valido escapo. 
          try{
              let response =  await addDoc(collection(db,'books'),this.form)
              console.log(response)
              //redirección
              this.$router.push('/');
          }
          catch(error){
              console.log(error)
          }
      }
      
    },
    // watch: {},
    // components: {},
    // mixins: [],
    // filters: {},
    // -- Lifecycle Methods
    // -- End Lifecycle Methods
  };
  </script>
  
  <style scoped>
  </style>