<template>
    <v-app-bar app color="primary" dark>
      <div class="d-flex align-center">
        <v-img
          alt="Vuetify Logo"
          class="shrink mr-2"
          contain
          src="https://cdn.vuetifyjs.com/images/logos/vuetify-logo-dark.png"
          transition="scale-transition"
          width="40"
        />
  
        <v-img
          alt="Vuetify Name"
          class="shrink mt-1 hidden-sm-and-down"
          contain
          min-width="100"
          src="https://cdn.vuetifyjs.com/images/logos/vuetify-name-dark.png"
          width="100"
        />
      </div>
  
      <v-spacer></v-spacer>
  
      <v-btn
        to="/book"
        text
      >
      <v-icon>mdi-plus-circle</v-icon>
        <span class="mr-2">Nuevo Libro</span>
        
      </v-btn>
      <v-btn color="info" @click="logout">Cerrar sesión</v-btn>
    </v-app-bar>
  </template>
  
  <script>
  import {getAuth, signOut} from 'firebase/auth'
  export default {
    name: "nav-bar",
    // props: {},
    data: function () {
      return {};
    },
    // computed: {},
    methods: {
      logout(){
        const auth = getAuth()
        signOut(auth)
          .then(()=>{
            //signout success
            this.$router.push('/login')
          })
          .catch(error=>{
            console.log(error)
          })
      }
    }
    // watch: {},
    // components: {},
    // mixins: [],
    // filters: {},
    // -- Lifecycle Methods
    // -- End Lifecycle Methods
  };
  </script>
  
